import React from 'react';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Alert from 'react-bootstrap/Alert';

import ApiService from './../../service/APIService';
import { Container, FormGroup } from 'react-bootstrap';

import "./style.css";

class Login extends React.Component {

    apiService = new ApiService();
   

    constructor(props) {

        super(props);

        this.state = {
            username: "",
            password: "",
            showNotification: false,
            errorMessage: ""
        }

    }

    componentDidMount() {
        
    }

    changeUsername(event) {
        this.setState({"username" : event.target.value})
    }

    changePassword(event) {
        this.setState({"password" : event.target.value})
    }

    login() {
        var payload = {
            username : this.state.username,
            password: this.state.password
        }

        var url = this.apiService.getApiUrl("login");
        var root = this;
        this.apiService.invokePost(url, JSON.stringify(payload), this, function(response) {
            try{

                if(response.status == "success") {
                    sessionStorage.setItem("loggedin", 1);
                    sessionStorage.setItem("user", JSON.stringify(response.data));
                    window.location.href = "/portal/dashboard";
                } else {
                    root.setState({
                        errorMessage: response.message,
                        showNotification: true
                    })

                }

            } catch(e) {
                console.error(e);
                root.setState({
                    errorMessage: "Server Response Error!!",
                    showNotification: true
                })
            }
        })

    }
                
    render() {

        var root = this;
        return (
            <Container className="login-container" fluid>
                <div class="alert-container">
                    <Alert key="alert" variant="danger" className={(this.state.showNotification ? 'show' : 'hidden')}>
                        {this.state.errorMessage}
                    </Alert>
                </div>
                <div className="overlay"></div>
                <div className="inclined-parent">
                    <div className="local-logo-area">
                        {/*<img src="../../assets/img/local-logo.png" />*/}
                    </div>
                    <div className="local-tag-line-section">
                        <p>Live better, work union </p>
                        <p>
                        Phone:  780-426-3375 / 403-250-1060
                        <br />
                        Email: info@local8.ca
                        </p>
                    </div>
                </div>

                <div className="login-card">

                    <div className="login-card-overlay"></div>
                    <div className="kmr-logo-section">
                        <div className="kmr-logo-image-container">
                            {/*<img src="../assets/img/kmr-logo.png"/>*/}
                        </div>
                        <hr />
                        <div id="login-inputs" >
                            <div className="label-container">
                                <label>Username: </label>
                            </div>
                            <input id="username" className="kmr-input" type="text" placeholder="Enter Username" value={this.state.username} onChange={this.changeUsername.bind(this)}/>
                            <br /> <br />
                            <div className="label-container">
                                <label>Password: </label>
                            </div>
                            <input id="password" className="kmr-input" type="password" placeholder="Enter Password" value={this.state.password} onChange={this.changePassword.bind(this)}/>
                            <br /> <br />

                            <div className="rememberme-section">
                                &nbsp; &nbsp;
                                <input id="rememberme" type="checkbox" /> &nbsp;
                                <label> Remember Me </label>
                                &nbsp; &nbsp;
                                <a href="#"> Forgot Password </a>
                            </div>
                            <br /> <br />
                            <button className="kmr-button" onClick={this.login.bind(this)}> Login </button>
                        </div>
                    </div>

                </div>
            </Container>
        )
    }
}

export default Login;