import React from 'react';
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom'; 

import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Badge from 'react-bootstrap/Badge';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';

import KMRInitialTabContent from './KMRInitialTabConent';
import KMRPendingInfoTabContent from './KMRPendingInfoTabContent';
import KMRPendingApprovalTabContent from './KMRPendingApprovalTabContent';
import KMRProcessingTabContent from './KMRProcessingTabContent';
import KMRErrorTabContent from './KMRErrorTabContent';
import KMRAcceptedTabContent from './KMRAcceptedTabContent';
import KMRRejectedTabContent from './KMRRejectedTabContent';
import KMRArchieveTabContent from './KMRArchieveTabContent';

import "../styles/KMRClaims.css";
import {AsyncTypeahead} from "react-bootstrap-typeahead";
import ApiService from "../service/APIService";


class KMRClaims extends React.Component {

    apiService = new ApiService();

    constructor(props) {
        super(props);

        this.formModalRef = React.createRef();

        this.state = {
            toCreate: false,

            initialCount: "..",

            //filters
            claimId: '',
            memberSSN: '',
            submissionDate: '',

            //child attr
            attrClaimId: '',
            attrMemberSSN: '',
            attrSubmissionDate: '',

            isLoading: false,
            options: []

        }
        this.goToCreateClaim = this.goToCreateClaim.bind(this);
    }

    updateFilterField(event) {
        var name = event.target.dataset.name;
        var obj = {};
        obj[name] = event.target.value;

        this.setState(obj);
    }

    filterClaims() {
        this.setState({
            attrClaimId: this.state.claimId,
            attrMemberSSN: this.state.memberSSN,
            attrSubmissionDate: this.state.submissionDate
        })
    }

    resetFilter() {
        this.setState({
            claimId: "",
            memberSSN: "",
            submissionDate: "",
            attrClaimId: "",
            attrMemberSSN: "",
            attrSubmissionDate: ""
        })
    }

    setMemberSSN(selectedMember) {
        if(selectedMember == undefined) {
            this.setState({
                memberSSN : ""
            })
            return;
        }
        this.setState({
            memberSSN:selectedMember.ssn
        })
    }

    updateInitialCount(count) {
        this.setState({
            initialCount: count
        })
    }

    render() {
        
        if (this.state.toCreate) {
            return <Redirect to='/portal/create' />
        }

        return (
            <>
                <div className="claims-container">

                    <Row>
                        <Col>
                            <h3> Claim System Dashboard </h3>
                        </Col>
                        <Col>
                            <Button className="create-claim-btn mateffect" variant="primary" onClick={this.goToCreateClaim}> 
                                Create Claim 
                            </Button>
                        </Col>
                    </Row>

                    <Row className="filter-container">
                        <Col>
                            <Accordion>
                                <Card>
                                    <Accordion.Toggle as={Card.Header} eventKey="0">
                                        Filters
                                    </Accordion.Toggle>

                                    <Accordion.Collapse eventKey="0">
                                        <Card.Body>
                                            <Form>
                                                <Form.Row>
                                                    <Form.Group as={Col} controlId="formGridClaimid">
                                                        <Form.Label>Claim Id</Form.Label>
                                                        <Form.Control
                                                            type="number"
                                                            placeholder="Enter Claim Id"
                                                            data-name="claimId"
                                                            value={this.state.claimId}
                                                            onChange={this.updateFilterField.bind(this)}
                                                        />
                                                    </Form.Group>

                                                    <Form.Group as={Col} controlId="formGridMembername">
                                                        <Form.Label>Member SSN / Name</Form.Label>
                                                        <AsyncTypeahead
                                                            id="member-auto-suggest"
                                                            placeholder="Search Name / SSN"
                                                            maxResults='6'
                                                            autoFocus={this.state.memberAutoSuggestFocused}
                                                            value={this.state.memberSSN}
                                                            isLoading={this.state.isLoading}
                                                            labelKey={option => `${option.ssn} ${option.fname} ${option.lname}`}
                                                            onSearch={(query) => {

                                                                this.setState({isLoading: true});
                                                                fetch(this.apiService.getApiUrl("memberSuggestUrl", [query]))
                                                                    .then(resp => resp.json())
                                                                    .then(json => {
                                                                        this.setState({
                                                                            options: json.data
                                                                        });
                                                                        this.setState({isLoading: false});
                                                                    });
                                                            }}

                                                            onChange={(selected) => {

                                                                this.setMemberSSN(selected[0])
                                                            }}

                                                            renderMenuItemChildren={(option, props) => (
                                                                <div className="suggestionContainer">
                                                                    <span>{option.fname} {option.lname}</span>
                                                                    <span className="smallSSNText"> (SSN : {option.ssn}) </span>
                                                                </div>
                                                            )}
                                                            options={this.state.options}
                                                        />
                                                    </Form.Group>

                                                    {/*<Form.Group as={Col} controlId="formGridStatus">
                                                        <Form.Label>Status</Form.Label>
                                                        <Form.Control as="select" placeholder="Enter Calim Id">
                                                            <option> Initial </option>
                                                            <option> Pending Information </option>
                                                            <option> Pending Approval </option>
                                                            <option> Processing </option>
                                                            <option> Error </option>
                                                            <option> Approved </option>
                                                            <option> Reject </option>
                                                        </Form.Control>
                                                    </Form.Group> */}

                                                    <Form.Group as={Col} controlId="formGridSubmissiondate">
                                                        <Form.Label>Submission Date</Form.Label>
                                                        <Form.Control
                                                            type="date"
                                                            data-name="submissionDate"
                                                            placeholder="Submission Date"
                                                            value={this.state.submissionDate}
                                                            onChange={this.updateFilterField.bind(this)}
                                                        />
                                                    </Form.Group>
                                                </Form.Row>

                                                <Form.Row>
                                                    <Button variant="primary" onClick={this.filterClaims.bind(this)}>
                                                        Submit
                                                    </Button>

                                                    &nbsp; &nbsp; &nbsp;

                                                    <Button variant="primary" onClick={this.resetFilter.bind(this)}>
                                                        Reset
                                                    </Button>
                                                </Form.Row>
                                            </Form>
                                        </Card.Body>
                                    </Accordion.Collapse>
                                </Card>
                            </Accordion>
                        </Col>
                    </Row>

                    <Row className="tabs-container">
                        <Col>
                            <Tabs defaultActiveKey="initial" id="uncontrolled-tab-example">
                                <Tab eventKey="initial" 
                                    title={
                                            <React.Fragment>
                                                Initial
                                                &nbsp;
                                                <Badge variant='primary'>
                                                    {this.state.initialCount}
                                                </Badge>
                                            </React.Fragment>
                                }>
                                    <KMRInitialTabContent
                                        claimId={this.state.attrClaimId}
                                        memberSSN={this.state.attrMemberSSN}
                                        submissionDate={this.state.attrSubmissionDate}
                                        onPopulateData={this.updateInitialCount.bind(this)}
                                    >
                                    </KMRInitialTabContent>
                                </Tab>

                                <Tab eventKey="pendinginfo" 
                                    title={
                                            <React.Fragment>
                                                Pending Information
                                                &nbsp; <Badge variant='warning'>12</Badge>
                                            </React.Fragment>
                                }>
                                    <KMRPendingInfoTabContent
                                        claimId={this.state.attrClaimId}
                                        memberSSN={this.state.attrMemberSSN}
                                        submissionDate={this.state.attrSubmissionDate}
                                    ></KMRPendingInfoTabContent>
                                </Tab>
                                <Tab eventKey="pendingapproval" 
                                    title={
                                            <React.Fragment>
                                                Pending Approval
                                                &nbsp; <Badge variant='info'>37</Badge>
                                            </React.Fragment>
                                }>
                                    <KMRPendingApprovalTabContent
                                        claimId={this.state.attrClaimId}
                                        memberSSN={this.state.attrMemberSSN}
                                        submissionDate={this.state.attrSubmissionDate}
                                    ></KMRPendingApprovalTabContent>
                                </Tab>
                                <Tab eventKey="processing" 
                                    title={
                                            <React.Fragment>
                                                Processing
                                                &nbsp; <Badge variant='success'>6</Badge>
                                            </React.Fragment>
                                }>
                                    <KMRProcessingTabContent
                                        claimId={this.state.attrClaimId}
                                        memberSSN={this.state.attrMemberSSN}
                                        submissionDate={this.state.attrSubmissionDate}
                                    ></KMRProcessingTabContent>
                                </Tab>
                                <Tab eventKey="error" 
                                    title={
                                            <React.Fragment>
                                                Error
                                                &nbsp; <Badge variant='danger'>2</Badge>
                                            </React.Fragment>
                                }>
                                    <KMRErrorTabContent
                                        claimId={this.state.attrClaimId}
                                        memberSSN={this.state.attrMemberSSN}
                                        submissionDate={this.state.attrSubmissionDate}
                                    ></KMRErrorTabContent>
                                </Tab>

                                <Tab eventKey="approved" title="Approved">
                                    <KMRAcceptedTabContent
                                        claimId={this.state.attrClaimId}
                                        memberSSN={this.state.attrMemberSSN}
                                        submissionDate={this.state.attrSubmissionDate}
                                    ></KMRAcceptedTabContent>
                                </Tab>
                                <Tab eventKey="rejected" title="Rejected">
                                    <KMRRejectedTabContent
                                        claimId={this.state.attrClaimId}
                                        memberSSN={this.state.attrMemberSSN}
                                        submissionDate={this.state.attrSubmissionDate}
                                    ></KMRRejectedTabContent>
                                </Tab>
                                <Tab eventKey="archieve" title="Archieve">
                                    <KMRArchieveTabContent
                                        claimId={this.state.attrClaimId}
                                        memberSSN={this.state.attrMemberSSN}
                                        submissionDate={this.state.attrSubmissionDate}
                                    ></KMRArchieveTabContent>
                                </Tab>
                            </Tabs>
                        </Col>
                    </Row>
                </div>
            </>
        );
    }
    
    goToCreateClaim(e) {
        this.setState(() => ({ toCreate: true }))
    }
}

export default KMRClaims;